import React from 'react';
import './style.scss';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

export default function Slider({slider, language}){
    return (
        <div className="home-slider-1">
            <Carousel
                dynamicHeight={true}
                autoPlay={true}
                showIndicators={false}
                showThumbs={false}
                showArrows={false}
                infiniteLoop={true}
                interval={4000}
                transitionTime={1000}
                showStatus={false}
            >

                {
                    slider.map((e, index) => {
                        return <div key={index}>
                            <img alt="special category"
                                 src={e} />
                        </div>
                    })
                }
            </Carousel>
        </div>
    )
}

